          </div>
        </div>
        <!-- left-t E -->
        <div class="right-t">
          <div class="right-t-l">
            <div class="price">{{ index_td_r[0] }} {{ sellAndBuy.price }}{{ usdt }}</div>
            <div class="list">
              <div class="th flex">
                <div class="td">{{ index_td_r[1] }}</div>
                <div class="td">{{ index_td_r[2] }}</div>
                <div class="td">{{ index_td_r[3] }}</div>
              </div>
              <div
                class="tr flex"
                v-for="(item, index) in sellAndBuy.sell"
                :key="index"
