              >
                <div class="td sell">{{ item.title }}</div>
                <div class="td">{{ ffff(item.price, 4) }}</div>
                <div class="td">{{ ffff(item.number, 4) }}</div>
              </div>
              <div class="line">
                <span>{{ sellAndBuy.price }}</span>
                <span>≈ {{ sellAndBuy.cny }} {{ k_line_txt[1] }}</span>
              </div>
              <div
