              :class="[index === tabListRTitleIndex ? 'active' : '']"
              v-for="(item, index) in tabListRTitle"
              :key="index"
              @click="tabr(index)"
            >{{ item }}</div>
          </div>
          <div class="b">{{ db_1_txt[0] }} {{ ffff(wallet.balance) }} {{ usdt }}</div>

          <div class="right-b-content">
            <div class="right-c-l">
              <ao-deal-box
                btn-type="buy"
