                @event="createOrder"
              ></ao-deal-box>
            </div>
            <div class="right-c-r">
              <ao-deal-box
                btn-type="sell"
                :btn-text="db_btn[1]"
                :mark="2"
                :balance="ffff(wallet.positions)"
                :price-type="tabListRTitleIndex"
                :price="sellAndBuy.price"
                :wallet="wallet"
                :currency="sellAndBuy.currency"
                :setting="currencySetting"
                :deal-rate="deal_rate"
                :lot="lot"
                :set-price="setPrice"
