<script>
import AoNav from "@/components/AoNav";
import pagination from "@/components/pagination";
import AoDealBox from "./components/AoDealBox2";
import AoTv from "./components/AoTv";
import AoWidget from "./components/AoTv-widget";

import { getCurrencyPriceAndIncrease, getInfo } from "@/api/index";
import { createOrder, getWeiTuo, orderCancel } from "@/api/asset";
import { getSetting } from "@/api/user";

var TIMER = null;

export default {
  components: {
    AoNav,
    AoDealBox,
    AoPagination: pagination,
    AoTv
  },
  data() {
    return {
      db_1_txt: this.$t('deal.db_1_txt'),
      db_btn: this.$t("deal.db_btn"), // ['买入(做多)', '卖出(做空)'],
      usdt: "",
      td_lt: this.$t("deal.td_lt"), // ["币种", "最新价", "涨幅"],
      k_line_txt: this.$t("deal.k_line_txt"), //["≈", "CNY", "涨幅", "最高价", "最低价", "24H量"],
      table_btn: this.$t("deal.table_btn"), //["撤单"],
      index_td_l: this.$t("deal.index_td_l"), //["时间", "交易对象", "数量", "价格", "操作"],
      index_td_r: this.$t("deal.index_td_r"), //["最新价", "方向", "价格", "数量"],
      index_td_r2: this.$t("deal.index_td_r2"), //["全站交易", "时间", "价格", "交易量"],
      table_txt: this.$t("deal.table_txt"), //["交易已完成", "已撤单"],

      currencyPriceAndIncreaseIndex: 0,
      currencyPriceAndIncreaseList: [],
      sellAndBuy: {
        currency: "", // 币种
        price: "", // 当亲价格
        cny: "", // 兑换为人民币价格
        sell: [], // 卖10挡
        buy: [], // 买10挡
        high: "", // 最高价
        low: "", // 最低价
        ins: "", // 涨幅
        sum: "" // 24小时成交量
      },
      dealOrderList: [],

      tabListLTitleIndex: 0,
      tabListLTitle: this.$t('deal.tabListLTitle2'),

      tabListLEntrustIndex: 0,
      tabListLEntrust: this.$t('deal.tabListLEntrust'),

      tabListRTitleIndex: 0,
      tabListRTitle: this.$t('deal.tabListRTitle'),
      pathList:  this.$t('deal.pathList'), //['合约记录' , '交易记录'],

      wallet: {},

      weituoList: [],
      page: 1,
      total: 1,

      currencySetting: [],
      deal_rate: 0,
      lot: 1,

      widget: null,

      setPrice: ""
    };
  },
  mounted() {
    this.usdt = this.$store.getters.usdt
    localStorage.setItem("type2", "1min");
    // 获取最新价和涨幅
    this.getInfo();
    this.createTimer();

    this.getPI();
    this.socket();
    getSetting().then(res => {
      this.currencySetting = res;
    });
  },
  destroyed() {
    this.widget.remove();
  },
  methods: {
    getSymbol(currency) {
      return (currency || "ETH") + "/USDT";
    },
    createWidget() {
      this.$nextTick(() => {
        const container_id = "tradingview";
        const width = "100%";
        const height = 535;
        const priceScale = localStorage.getItem("type2");
        const symbol = this.symbol;
        var widget = (this.widget = new AoWidget({
          container_id,
          width,
          height,
          priceScale,
          symbol,
          type: 2
        }));
        widget.create().ready();
      });
    },
    path(path) {
      var token = this.$store.state.token;
      if (!token) {
        this.$message({
          type: "error",
          message: "请先登陆"
        });
        return;
      }
      localStorage.setItem("web-currency", this.sellAndBuy.currency);
      this.$router.push({ path: path });
    },
    createTimer() {
      TIMER && clearInterval(TIMER);
      TIMER = setInterval(() => {
        this.getWeiTuo();
        this.getInfo();
      }, 1000 * 2);
    },
    changeCueeency(index) {
      this.currencyPriceAndIncreaseIndex = index;
      this.sellAndBuy.currency = this.currencyPriceAndIncreaseList[index].name;
      this.sellAndBuy.price = "";
      this.symbol = this.getSymbol(this.sellAndBuy.currency);
      this.widget.remove();
      this.createWidget();
      this.getWeiTuo();
      this.createTimer();
    },
    changePrice(price) {
      this.setPrice = price;
    },
    getWeiTuo() {
      if (!this.$store.state.token) return;
      var data = {
        page: this.page,
        currency: this.getC(),
        mark: this.tabListLEntrustIndex + 1,
        wt: this.tabListLTitleIndex,
        type: 2
      };
      getWeiTuo(data).then(res => {
        this.weituoList = res.list;
        this.total = res.total;
      });
    },
    cancel(index, id) {
      orderCancel({ id }).then(() => {
        if (this.weituoList[index].id == id) {
          this.weituoList[index].status = 2;
        }
      });
    },
    createOrder(data) {
      var token = this.$store.state.token;
      if (!token) {
        this.$message({
          type: "error",
          message: "请先登陆"
        });
        return;
      }
      data.deal_type = 2;
      createOrder(data).then(res => {
        this.getInfo();
      });
    },
    // "限价交易", "市价交易"
    tabr(index) {
      this.tabListRTitleIndex = index;
    },
    // "当前委托", "历史委托"
    tabl(index) {
      this.tabListLTitleIndex = index;
      this.getWeiTuo();
    },
    // "买入", "卖出"
    tabe(index) {
      this.tabListLEntrustIndex = index;
      this.getWeiTuo();
    },
    pagechange(page) {
      this.page = page;
      this.getWeiTuo();
    },
    getC() {
      return this.currencyPriceAndIncreaseList.length > 0
        ? this.currencyPriceAndIncreaseList[this.currencyPriceAndIncreaseIndex]
            .name
        : "";
    },
    ffff(v = 0, l = 8) {
      l = 4
      return parseFloat(v).toFixed(l);
    },
    setValue(str) {
      var arr = str.split(",");
      if (arr[0] == this.symbol){
        this.sellAndBuy.symbol = arr[0];
        this.sellAndBuy.currency = arr[1];
        this.sellAndBuy.price = arr[2];
        this.sellAndBuy.cny = arr[3];
        this.sellAndBuy.high = arr[4];
        this.sellAndBuy.low = arr[5];
        this.sellAndBuy.ins = arr[6];
        this.sellAndBuy.sum = arr[7];
      }
      this.currencyPriceAndIncreaseList.forEach(obj => {
        if (obj.symbol == arr[0]) {
          obj.price = arr[2];
          obj.increase = arr[6];
        }
      });
    },
    setDeal(str) {
      var arr = str.split(",");
      var start = 1;
      var len = 10;
      var buy = [];
      for (; ; start += 2) {
        buy.push({
          title: "卖" + len,
          price: arr[start],
          number: arr[start + 1]
        });
        len--;
        if (len == 0) break;
      }

      start = 21;
      var len = 1;
      var sell = [];
      for (; ; start += 2) {
        sell.push({
          title: "买" + len,
          price: arr[start],
          number: arr[start + 1]
        });
        len++;
        if (len == 11) break;
      }
      this.sellAndBuy.buy = sell;
      this.sellAndBuy.sell = buy;
    },
    getInfo() {
      // if (!this.$store.state.token) return;
      var data = {
        currency: this.sellAndBuy.currency,
        type: 2
      };
      if (!data.currency) return;
      getInfo(data).then(res => {
        this.wallet = res.wallet;
        this.deal_rate = res.deal_rate;
        this.lot = res.lot;
        this.usdt = res.usdt;
        this.k_line_txt[1] = res.unit;
      });
    },
    getPI() {
      getCurrencyPriceAndIncrease({
        currency: this.sellAndBuy.currency || "",
        type: 2
      }).then(res => {
        this.symbol = res.price[0].symbol;
        this.sellAndBuy.currency = res.price[0].name;
        this.createWidget();
        this.currencyPriceAndIncreaseList = res.price;
        this.wallet = res.wallet || {};
        this.deal_rate = res.deal_rate;
        this.lot = res.lot;
        if(!this.usdt) this.usdt = res.usdt
        if(!this.k_line_txt[1]) this.k_line_txt[1] = res.cny
        this.setDeal(res.deal);
        this.setValue(res.value);
      });
    },
    socket() {
      __socket.emit("login", 'login');

      this.socket_daymarket();
      this.socket_market_depth();
    },
    // 最新价
    socket_daymarket() {
      __socket.on("daymarket", msg => {
        if (typeof msg === 'string') msg = JSON.parse(msg)
        if (msg.type == "daymarket") {
          var spt = ",";
          //交易对、币种、当前价格、人民币价格、最高价、最低价、涨幅、24小时成交量
          var keys = [
            "symbol",
            "currency_name",
            "now_price",
            "now_price",
            "high",
            "low",
            "change",
            "volume"
          ];
          var str = "";
          keys.forEach(function(key) {
            str += msg[key] + spt;
          });
          this.setValue(str);
        }
      });
    },
    // 买10 卖10
    socket_market_depth() {
      __socket.on("market_depth", msg => {
        if (typeof msg === 'string') msg = JSON.parse(msg)
       // console.log(msg)
        if (msg.type == "market_depth" && msg.symbol == this.symbol) {
          var symbol = msg.symbol;
          var asks = msg.asks; // 卖
          var bids = msg.bids; // 买
          var arr = asks.concat(bids);
          var str = symbol + ",";
          arr.forEach(function(o) {
            str = str + o[0] + "," + o[1] + ",";
          });
          this.setDeal(str);
        }
      });
    }
  },
  destroyed() {
    TIMER && clearInterval(TIMER);
    console.log("destroyed");
  }
};
</script>

