            display: flex;
            padding: 0 30px;
            color: #b0b8db;
            span {
              color: #b0b8db;
              font-size: 12px;
              &:first-child {
                color: #cc4951;
                font-size: 14px;
              }
            }
          }
        }
      }
      .right-t-r {
        .list .tr {
          line-height: 26px;
        }
      }
      // end
    }
    .right-b {
      margin-top: 3px;
      background: #1a243b;
      height: 585px;
      .b {
        height: 48px;
        border-bottom: 1px solid #303b4b;
        line-height: 48px;
        font-size: 14px;
        color: #c7cce6;
        padding: 0 20px;
      }
      .right-b-tab {
        padding: 12px 20px;
        box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
        display: flex;
        .right-b-tab-item {
          margin-right: 30px;
          cursor: pointer;
          padding-bottom: 5px;
          color: #c7cce6;
          &.active {
            color: #7a98f7;
            border-bottom: 1px solid #7a98f7;
          }
        }
      }
      .right-b-content {
        display: flex;
        .right-c-l,
        .right-c-r {
          width: 50%;
          font-size: 14px;
        }
        .right-c-l {
          padding: 0 0 0 25px;
        }
        .right-c-r {
          padding: 0 25px 0 15px;
        }
      }
    }
  }
}
</style>



