import TV from "./AoTv-p";
import createFeed from './AoTv-datafeed'
var TIMER = null
const BASE_URL = process.env.VUE_APP_BASE_API



function AoWidget({ container_id, width, height, priceScale, symbol, type = 2 }) {
    this.priceScale = priceScale
    this.symbol = symbol
    this.widget = null
    this.container_id = container_id
    this.width = width
    this.height = height
    this.type = type
    if (TIMER) clearInterval(TIMER)

    this.next_timeshar_url = BASE_URL + 'web/new_timeshar'  // 获取下一条记录

    // http://jyxt.ys.13mv.com/api/currency/new_timeshar?from=1565328534&to=1565414994&symbol=EOS/ETH&period=1min
    this.new_timeshar_url = 'http://jyxt.ys.13mv.com/api/currency/' + 'new_timeshar'    // 获取当前数据
}
AoWidget.prototype.connect = function (real) {
    // TIMER = setInterval(() => {
    let type = localStorage.getItem('type2')
    __socket.emit("tv", type);
    __socket.on('kline', (msg) => {
        if (typeof msg === 'string') msg = JSON.parse(msg)
        // console.log(msg)
        if (msg.type == 'kline' && msg.symbol == this.symbol && msg.period == type) {
            let obj = {}
            obj.open = Number(msg.open)
            obj.low = Number(msg.low)
            obj.high = Number(msg.high)
            obj.close = Number(msg.close)
            obj.volume = Number(msg.volume)
            obj.time = Number(msg.time)
            real(obj)
        }
    })
    // $.ajax({
    //     url: this.next_timeshar_url + '?period=' + type + '&symbol=' + this.symbol + '&type=' + this.type,
    //     type: 'get',
    //     success: function (msg) {
    //         if (msg) {
    //             let obj = {}
    //             obj.open = Number(msg.open)
    //             obj.low = Number(msg.low)
    //             obj.high = Number(msg.high)
    //             obj.close = Number(msg.close)
    //             obj.volume = Number(msg.volume)
    //             obj.time = Number(msg.time)
    //             real(obj)
    //         }
    //     }
    // })
    // }, 1000 * 2)
}
AoWidget.prototype.create = function () {
    console.log('create--------------------')
    var _this = this
    this.widget = new TradingView.widget({
        symbol: this.symbol,
        interval: 1,
        debug: false,
        fullscreen: false,
        autosize: true,
        container_id: this.container_id,
        // datafeed: new Datafeeds.UDFCompatibleDatafeed(
        //     "https://demo_feed.tradingview.com",
        //     1000 * 20000
        // ),
        datafeed: createFeed(this),
        library_path: "/static/tradeview/charting_library/",
        custom_css_url: "/static/tradeview/new.css",
        locale: "zh",
        width: this.width,
        height: this.height,
        drawings_access: {
            type: "black",
            tools: [{ name: "Regression Trend" }]
        },
        disabled_features: TV.getDisabledFeatures(),
        enabled_features: TV.getEnabledFeatures(),
        //charts_storage_url: 'http://saveload.tradingview.com',
        charts_storage_api_version: "1.1",
        toolbar_bg: "transparent",
        timezone: "Asia/Shanghai",
        studies_overrides: {
            "volume.precision": "1000"
        },
        // enable_publishing: false,
        // hide_legend: true,
        // save_image: false,
        // supports_search: false,
        overrides: TV.getOverrides()
    });
    return this
}
AoWidget.prototype.ready = function () {
    var _this = this
    const widget = this.widget
    widget.MAStudies = [];
    widget.selectedIntervalButton = null;
    // widget.setLanguage('en')
    widget.onChartReady(function () {
        //图表方法
        // document.getElementById('trade-view').childNodes[0].setAttribute('style', 'display:block;width:100%;height:100%;');
        //let that =this
        widget
            .chart()
            .createStudy(
                "Moving Average",
                false,
                true,
                [15, "close", 0],
                null,
                { "Plot.color": "#e843da" }
            );
        widget.chart().createStudy("MA Cross", false, false, [10, 20]);
        let buttonArr = TV.buttonArr;
        let btn = {};
        let nowTime = "";
        buttonArr.forEach((v, i) => {
            let button = $(widget.createButton());
            button
                .attr("title", v.text)
                .addClass("my2")
                .text(v.text);

            if (v.text == "1分钟") {
                button.css({ "background-color": "#2c3b59", color: "#c7cce6" });
                localStorage.setItem("tim", "1"); //默认为1分钟
                localStorage.setItem("type2", "1min"); //默认为1分钟
            }

            // console.log($(this),'999999')
            btn = button.on("click", function (e) {
                $(this)
                    .parents(".wrap-3tiHesTk-")
                    .children()
                    .find(".my2")
                    .removeClass("mydate")
                    .removeAttr("style"); //去掉1分钟的
                handleClick(e, v.value, v.type);
                widget.chart().setChartType(v.chartType); //改变K线类型
            });
        });
        let handleClick = (e, value, type) => {
            _this.clearTimer()
            // __socket.setSub(type)

            //_this.setSymbol = function (symbol, value) {
            // gh.chart().setSymbol(_this.symbol, value);
            //};
            localStorage.setItem("tim", value);
            localStorage.setItem("type2", type);
            widget.chart().setResolution(value, function onReadyCallback() { }); //改变分辨率

            $(e.target)
                .addClass("mydate")
                .closest("div.space-single")
                .siblings("div.space-single")
                .find("div.button")
                .removeClass("mydate");
        };
    });
}
AoWidget.prototype.update = function (item) {
    for (var key in item) {
        this[key] = item[key]
    }
    this.clearTimer()

    this.remove();
    this.create();
}
AoWidget.prototype.clearTimer = function () {
    if (TIMER) clearInterval(TIMER)
}
AoWidget.prototype.remove = function () {
    this.clearTimer()

    if (this.widget) {
        this.widget.remove();
        this.widget = null;
    }
}


export default AoWidget


