<template>
  <div class="ao-deal2">
    <!-- <div class="b">可用 {{ balance }} {{ get_c_1 }}</div> -->
    <el-form ref="form" :model="formBuy" label-width="50px">
      <el-form-item :label="db_2_txt[0]">
        <el-input
          v-model="formBuy.price"
          :disabled="!!priceType"
          :placeholder="pl"
          @focus="focus"
          @blur="blur"
        >
          <template slot="append">{{ usdt }}</template>
        </el-input>
      </el-form-item>
      <el-form-item :label="db_2_txt[1]">
        <el-select v-model="formBuy.multiple" :placeholder="db_2_txt[2]">
          <el-option
            v-for="item in getSettingA"
            :key="item.id"
            :label="item.value"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item :label="db_2_txt[3]">
        <el-row class="lot">{{ db_2_txt[4] }}{{ ffff(lot, 4) }}{{ currency }}</el-row>
        <el-input v-model="formBuy.amount">
          <template slot="append">{{ db_2_txt[5] }}</template>
        </el-input>
      </el-form-item>
      <el-form-item v-if="getSettingB.length > 0">
        <div
          class="shoushu"
          :class="btnType"
          v-for="item in getSettingB"
          :key="item.id"
          @click="choose(item.value)"
        >{{ item.value }}</div>
      </el-form-item>
      <!-- <el-form-item label="密码">
        <el-input v-model="formBuy.password" placeholder="请输入交易密码"></el-input>
      </el-form-item>-->
    </el-form>
    <div class="c">
      <span>{{ db_2_txt[6] }}</span>
      <span>≈ {{ ffff(formBuy.price*formBuy.amount*lot, 4) }} {{ usdt }}</span>
    </div>
    <div class="c">
      <span>{{ db_2_txt[7] }}</span>
      <span>≈ {{ ffff(formBuy.price*formBuy.amount*lot / (formBuy.multiple || 1), 4) }} {{ usdt }}</span>
    </div>
    <!-- <div class="c">
      <span>交易服务费</span>
      <span>≈ {{ ffff(price*formBuy.amount*lot*dealRate, 4) }} USDT</span>
    </div> -->
    <el-button :class="btnType" type="primary" @click="event">{{ btnText }}{{ currency }}</el-button>
  </div>
</template>

<script>
export default {
  props: {
    usdt: String,
    currency: String,
    mark: Number,
    btnType: String,
    btnText: String,
    lot: Number,
    dealRate: {
      type: Number,
      default: 0
    },
    setting: {
      type: Array,
      default: () => {
        return [];
      }
    },
    priceType: {
      type: Number,
      default: 0
    },
    price: {
      type: String,
      default: "10"
    },
    balance: {
      type: String,
      default: "0.0000"
    },
    setPrice: {
      type: String,
      default: ""
    }
  },
  computed: {
    getSettingA() {
      return this.setting.filter(o => {
        return o.classify === 1;
      });
    },
    getSettingB() {
      return this.setting.filter(o => {
        return o.classify === 2;
      });
    },
    get_c_1() {
      return this.mark === 1 ? this.usdt : this.currency;
    },
    pl() {
      return this.priceType === 0
        ? ""
        : this.btnType === "buy"
        ? this.db_2_txt[8]
        : this.db_2_txt[9]
    }
  },
  watch: {
    setPrice(a, b) {
      if (a != b && a) {
        this.is_focus = true;
        this.formBuy.price = a;
      }
    },
    price() {
      if (!this.is_focus) this.formBuy.price = this.price;
    },
    priceType() {
      if (!this.is_focus)
        this.formBuy.price = this.priceType === 0 ? this.price : "";
    }
  },
  data() {
    var that = this;
    return {
      db_2_txt: this.$t('deal.db_2_txt'),
      is_focus: false,
      total: "0.0000",
      formBuy: {
        multiple: "",
        price: that.price,
        amount: "0",
        password: ""
      }
    };
  },
  methods: {
    focus() {
      this.is_focus = true;
    },
    blur() {
      if (!this.formBuy.price) {
        this.is_focus = false;
      }
    },
    choose(value) {
      this.formBuy.amount = value;
    },
    ffff(v = 0, l = 8) {
      l = 4
      return parseFloat(v).toFixed(l);
    },
    event() {
      this.formBuy.price_type = this.priceType;
      this.formBuy.mark = this.mark;
      this.formBuy.currency = this.currency;
      this.$emit("event", this.formBuy);
    }
  }
};
</script>

<style lang="scss">
.ao-deal2 {
  .lot {
    color: #637085;
    font-size: 14px;
  }
  .b {
    height: 48px;
    border-bottom: 1px solid #303b4b;
    line-height: 48px;
    font-size: 14px;
    color: #c7cce6;
  }
  .c {
    display: flex;
    justify-content: space-between;
  }
  .c span {
    color: #c7cce6;
    margin-top: 10px;
    font-size: 14px;
  }
  .el-form-item {
    position: relative;
    line-height: 30px;
    margin-top: 15px;
    margin-bottom: 0;
  }
  .shoushu {
    display: inline-block;
    font-weight: 400;
    border: 1px solid #2e3753;
    border-radius: 2px;
    font-size: 14px;
    color: #fff;
    line-height: 1.2;
    width: 23.5%;
    text-align: center;
    padding: 5px 0;
    margin-right: 3.14px;
    cursor: pointer;
    &.buy {
      background-color: #12b886;
      border: 1px solid #12b886;
    }
    &.sell {
      background-color: #cc4951;
      border: 1px solid #cc4951;
    }
  }
  .el-form-item__label {
    text-align: left;
    padding: 0;
    font-size: 14px;
    color: #637085;
  }
  .el-input__inner {
    border: 1px solid #2e3753;
    border-radius: 3px;
    height: 38px;
    text-indent: 15px;
    font-size: 16px;
    color: #cdd6e4;
    background-color: #1a243b;
    line-height: 38px;
    padding: 0;
  }
  .el-input.is-disabled .el-input__inner {
    background-color: #1a243b;
    color: #2e3753;
    border: 1px solid #2e3753;
    color: 12px;
  }
  .el-input-group__append {
    color: #637085;
    font-size: 16px;
    border: none;
    padding: 0 0 0 8px;
    background: transparent;
  }
  .el-slider__runway {
    background-color: #2e3244;
  }
  .el-button {
    width: 100%;
    height: 40px;
    border-radius: 3px;
    color: #cdd6e4;
    border: none;
    margin-top: 10px;
    font-size: 16px;
    cursor: pointer;
    &.buy {
      background-color: #12b886;
    }
    &.sell {
      background-color: #cc4951;
    }
  }
}

.el-scrollbar__bar::-webkit-scrollbar {
  width: 6px;
  background-color: transparent;
}
.el-scrollbar__bar::-webkit-scrollbar-thumb {
  border-radius: 3px;
  background-color: transparent;
}
</style>



